* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #fafbff;
  font-family: Poppins, sans-serif;
  font-size: 14px;
  font-weight: 500;
  display: flex;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

button {
  cursor: pointer;
  padding: 0;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

.sidebar {
  width: 250px;
  background-color: #fff;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  display: flex;
}

.sidebar_logo_container {
  gap: 8px;
  margin-bottom: 64px;
  display: flex;
}

.sidebar_title_container {
  gap: 4px;
  display: flex;
}

.sidebar_title {
  color: #000;
  font-size: 26px;
  font-weight: 600;
}

.sidebar_subtitle {
  color: #838383;
  height: 10px;
  margin-top: 18px;
  font-size: 10px;
}

.sidebar_nav, .sidebar_menu {
  width: 100%;
}

.sidebar_nav {
  margin-bottom: auto;
}

.sidebar_item {
  cursor: pointer;
  justify-content: space-between;
  gap: 14px;
  margin-bottom: 20px;
  transition: background-color .3s ease-in-out, padding .3s ease-in-out, border-radius .3s ease-in-out, color .3s ease-in-out;
  display: flex;
}

.sidebar_item:hover {
  color: #fff;
  background-color: #5932ea;
  border-radius: 8px;
  padding: 12px 8px;
}

.sidebar_item:last-child {
  margin-bottom: 0;
}

.sidebar_link_container {
  gap: 14px;
  display: flex;
}

.sidebar_link {
  color: #9197b3;
  font-size: 14px;
  transition: color .3s ease-in-out;
}

.sidebar_item:hover .sidebar_link {
  color: #fff;
}

.sidebar_item_icon {
  fill: #9197b3;
  transition: fill .3s ease-in-out;
}

.sidebar_item:hover .sidebar_item_icon {
  fill: #fff;
}

.sidebar_item_icon_second, .sidebar_icon_chevron {
  stroke: #9197b3;
  transition: stroke .3s ease-in-out;
}

.sidebar_item:hover .sidebar_item_icon_second, .sidebar_item:hover .sidebar_icon_chevron {
  stroke: #fff;
}

.sidebar_item_icon_user {
  fill: #9197b3;
  stroke: #9197b3;
  transition: fill .3s ease-in-out, stroke .3s ease-in-out;
}

.sidebar_item:hover .sidebar_item_icon_user {
  fill: #fff;
  stroke: #fff;
}

.sidebar_profile {
  gap: 12px;
  display: flex;
}

.sidebar_profile_img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

.sidebar_profile_name {
  display: block;
}

.sidebar_profile_role {
  color: #757575;
  font-size: 12px;
  font-weight: 400;
}

.main_content {
  flex: 1;
  padding: 20px 70px;
}

.main_content_header {
  margin-bottom: 50px;
}

.main_content_greeting {
  font-size: 24px;
}

.customers {
  background-color: var(--primary-color-white);
  border-radius: 30px;
  padding: 30px 38px;
  box-shadow: 0 4px 8px rgba(226, 236, 249, .5);
}

.customers_header {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 40px;
  display: flex;
}

.customers_title_container {
  flex-direction: column;
  gap: 8px;
  display: flex;
}

.customers_title {
  font-size: 22px;
  font-weight: 600;
}

.customers_subtitle {
  color: #16c098;
  font-weight: 400;
}

.customers_input_container {
  position: relative;
}

.customers_search_icon {
  position: absolute;
  top: 7px;
  left: 8px;
}

.customers_input {
  color: #292d32;
  resize: none;
  background-color: #f9fbff;
  border: 1px solid #e7e7e7;
  border-radius: 10px;
  outline: rgba(0, 0, 0, 0);
  padding: 10px 8px 10px 40px;
  transition: border-color .3s ease-in-out;
}

.customers_input:focus {
  border-color: #5932ea;
}

.customers_input::-ms-input-placeholder {
  color: #b5b7c0;
}

.customers_input::placeholder {
  color: #b5b7c0;
}

.customers_table_container {
  margin-bottom: 32px;
  overflow-x: auto;
}

.customers_table {
  width: 100%;
  border-collapse: collapse;
}

.customers_table th, .customers_table td {
  text-align: left;
  border-bottom: 1px solid #eee;
  padding: 12px;
}

.customers_table th {
  color: #b5b7c0;
  font-weight: 500;
}

.customers_table td {
  color: #292d32;
}

.status {
  text-align: center;
  border-radius: 4px;
  padding: 4px 12px;
  display: block;
}

.status_active {
  color: #008767;
  background-color: rgba(22, 192, 152, .4);
  border: 1px solid #008767;
}

.status_inactive {
  color: #df0404;
  background-color: #ffc5c5;
  border: 1px solid #df0404;
}

.customers_under_container {
  justify-content: space-between;
  display: flex;
}

.customers_under_text {
  color: #b5b7c0;
}

.customers_nav_buttons_container {
  gap: 12px;
  display: flex;
}

.customers_nav_button {
  color: #404b52;
  width: 24px;
  height: 24px;
  background-color: #f5f5f5;
  border: 1px solid #eee;
  border-radius: 4px;
  font-family: Poppins, sans-serif;
  font-size: 12px;
  transition: color .3s ease-in-out, background-color .3s ease-in-out, border .3s ease-in-out;
  display: block;
}

.customers_nav_button:hover, .button_active {
  color: #fff;
  background-color: #5932ea;
  border: none;
}

.customers_nav_dots {
  height: 12px;
  margin-top: 8px;
  font-size: 12px;
}

@media screen and (max-width: 1024px) {
  .sidebar {
    width: 80px;
  }

  .sidebar_title, .sidebar_subtitle, .sidebar_link, .sidebar_icon_chevron {
    display: none;
  }

  .sidebar_item {
    gap: 0;
  }

  .sidebar_profile {
    flex-direction: column;
  }

  .main_content {
    padding: 15px;
  }

  .customers {
    border-radius: 20px;
    padding: 20px;
  }

  .customers_title {
    font-size: 20px;
  }

  .customers_input {
    width: 100%;
  }
}

@media screen and (max-width: 832px) {
  .sidebar {
    display: none;
  }

  .main_content {
    padding: 10px;
  }

  .customers {
    border-radius: 15px;
    padding: 15px;
  }

  .customers_title {
    font-size: 18px;
  }

  .customers_header {
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
  }

  .customers_input_container {
    width: 100%;
  }

  .customers_table_container {
    overflow-x: auto;
  }

  .customers_table th, .customers_table td {
    padding: 10px;
    font-size: 12px;
  }

  .customers_nav_buttons_container {
    gap: 8px;
  }

  .customers_nav_button {
    width: 22px;
    height: 22px;
  }
}

